import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { SideNavItems, SideNavSection } from '@modules/navigation/models';
import { TableData, Response } from './base.model';
import { tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class CoreService {

  constructor(private httpClient: HttpClient) { }

  private getConfigPromise(){
    const url = 'assets/config.json';
    let headers = new HttpHeaders();
    headers = headers.append('Refresh', 'true');
    return this.httpClient.get<Config[]>(url, { headers }).toPromise();
  }
  public loadConfig(){
    console.log("Loading configuration...");
    this.getConfigPromise().then(data => {
      data.forEach(e => {
        localStorage.setItem(e.name, e.value);
      });
    });
  }

  public getAppTitle(){
    return localStorage.getItem('title')!;
  }
  public getBaseURL(){
    return localStorage.getItem('baseURL')!;
  }
  public getBaseAPIURL(){
    return localStorage.getItem('apiBaseURL')!;
  }
  public isStatic(){
    return localStorage.getItem('static') === '1';
  }
  public isMenuVisible(){
    return localStorage.getItem('tmenu') === '1';
  }

  public GetHeaders(): HttpHeaders{
    let headers = new HttpHeaders();
    headers = headers.append('Refresh', 'true');
    headers = headers.append('Access-Control-Allow-Origin', '*');
    return headers;
  }

  public async get_snsections(){
    let url = this.getBaseAPIURL() + '/sb/snsections';
    if(this.isStatic()){
      url = 'assets/data/snsections.json';
    }

    const headers = this.GetHeaders();
    return await this.httpClient.get<SideNavSection[]>(url, { headers }).toPromise();
  }
  public async get_snitems(){
    let url = this.getBaseAPIURL() + '/sb/snitems';
    if(this.isStatic()){
      url = 'assets/data/snitems.json';
    }

    const headers = this.GetHeaders();
    return await this.httpClient.get<SideNavItems>(url, { headers }).toPromise();
  }

  public CanGetData() : boolean {
    const temp= sessionStorage.getItem('currentURL');
    const splitted = JSON.parse(temp??'[]');
    return (splitted[1]==='forms')||(splitted[1]==='details');
  }
  public GetIdmenu(){
    const idmenu= parseInt(sessionStorage.getItem('idmenu')??"-1", 0);
    return idmenu;
  }
  public GetIdmenu2(){
    const temp= sessionStorage.getItem('currentURL');
    const splitted = JSON.parse(temp??'[]');
    let idmenu;
    if(splitted[1]==='forms'){
      idmenu = splitted[splitted.length - 2];
    }else{
      idmenu = splitted[splitted.length - 1];
    }
    return idmenu;
  }
  public async GetMetaData() {
    // console.log(this.GetIdmenu());
    const idmenu = this.GetIdmenu();
    let url = this.getBaseAPIURL() + '/app/301/metadata/'+ idmenu;
    if(this.isStatic()){
      url = '/assets/data/data.json';
    }

    const headers = this.GetHeaders();
    return await this.httpClient.get<TableData>(url, { headers }).toPromise();
  }

  public Save(param: any){
    this.Set(param).subscribe(
      data => {
        if (data.success && data.success === true) {
          console.log(data);
        } else {
          // console.log(data);
        }
      },
      (error) => {
        console.log(error.message);
      }
    );
  }

  public Set(param: any): Observable<any> {
    const idmenu = this.GetIdmenu();
    const url = this.getBaseAPIURL() + '/app/';

    let headers = new HttpHeaders();
    headers = headers.append('Refresh', 'true');
    headers = headers.append('Access-Control-Allow-Origin','*');
    const options = {headers };

    const body = {
      idApp: 301,
      idMenu: idmenu,
      data: param
    };
    // console.log(body);

    return this.httpClient.post<any>(url, body, {headers})
      .pipe(
        tap(response => {
          // console.log(response);
        })
      );
  }

}

export class Config {
  public name: string = '';
  public value: string = '';
}

