import { Component } from '@angular/core';
import { Event as NavigationEvent, NavigationStart } from "@angular/router";
import { Title } from '@angular/platform-browser';
import { ChildActivationEnd, Router } from '@angular/router';
import { filter } from 'rxjs/operators';
import { AppParameter, NavIdentification } from './app.parameter';
import { CoreService } from '@modules/services/base.service';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
})
export class AppComponent {
    title = 'sb-admin-angular';
    constructor(public router: Router,
        private coreService: CoreService,
        private titleService: Title,
        private param: AppParameter) {

            this.coreService.loadConfig();
            this.router.events
            .pipe(filter(event => event instanceof ChildActivationEnd))
            .subscribe(event => {
                let snapshot = (event as ChildActivationEnd).snapshot;
                while (snapshot.firstChild !== null) {
                    snapshot = snapshot.firstChild;
                }
                this.titleService.setTitle(snapshot.data.title || 'SB Admin Angular');
            });
        this.router.events
			.pipe(
				filter(
					(event: NavigationEvent ) => {
						return(event instanceof NavigationStart );
					}
				)
            )
            .subscribe((event) => {
                const e = event as NavigationStart;
                const navId = new NavIdentification();
                navId.url =  e.url.toString();
                const splitted = navId.url.split("/");
                sessionStorage.setItem('currentURL', JSON.stringify(splitted));
                this.param.sendFunctionEvent(navId);
        });
    }

}


